.link {
    text-decoration: none;
    color: inherit;
}

.card {
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
    position: relative;
}

.price {
    display: flex;
    align-items: center;
}

.name {
    height: 48px;
    margin-top: auto;
    width: 100%;
    text-align: center;
}